/* -----------------------------------------------------
    copyright style
----------------------------------------------------- */
.copyright-inner {
  background: #061847;
  padding: 8px 0;
}

.copyright-inner .copyright-text {
  text-align: center;
  font-size: 14px;
  letter-spacing: 0.28px;
  color: #ffffff;
  line-height: 44px;
}

.copyright-inner .copyright-text a {
  font-size: 14px;
  letter-spacing: 0.42px;
  color: #ffffff;
  font-weight: 500;
  display: inline-block;
}

.copyright-inner .copyright-text a i {
  color: var(--main-color-one);
  padding: 0 3px 0 5px;
}

.copyright-inner .copyright-text a span {
  position: relative;
}

.copyright-inner .copyright-text a span:before {
  position: absolute;
  left: auto;
  right: 0;
  top: 100%;
  content: '';
  background: #ffffff;
  height: 1px;
  width: 0%;
  visibility: hidden;
  opacity: 0;
  -ms-transform: translateY(-50%);
  /* IE 9 */
  -webkit-transform: translateY(-50%);
  /* Chrome, Safari, Opera */
  transform: translateY(-50%);
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

.copyright-inner .copyright-text a span:hover:before {
  visibility: visible;
  opacity: 1;
  right: auto;
  left: 0;
  width: 100%;
}

